html, body {
  margin: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

:root {
  --primary-color: #707b71;
  --secondary-color: #4f5c4f;
  --background-color: #707b71;
  --darker-text-on-background-color: #fff;
  --seed-color: #e5edff;
  --text-color: #fff;
  --text-on-primary-color: #e5edff;
}

.sr-only {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute;
  top: -9999px;
  left: -9999px;
}

body {
  background: var(--background-color);
  color: var(--text-color);
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  position: relative;
}

.modal-bg {
  opacity: 0;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background: #000c;
  transition: opacity .2s linear;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.help-modal {
  opacity: 0;
  z-index: 200;
  width: 40%;
  max-height: 600px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 16px;
  flex-direction: column;
  padding: 1rem;
  transition: opacity .2s ease-out, transform .5s cubic-bezier(.21, .65, .68, .19);
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 1px 1px 1px #0000004d;
}

@media only screen and (max-width: 760px) {
  .help-modal {
    width: 80%;
    max-height: 90%;
  }
}

.help-modal__header {
  text-align: center;
  border-bottom: 1px solid var(--secondary-color);
  margin: 0 0 1rem;
  font-size: 1rem;
}

.help-modal-close {
  color: var(--secondary-color);
  font: inherit;
  cursor: pointer;
  margin: 1rem;
}

.help-modal-close:hover, .help-modal-close:active {
  color: var(--primary-color);
}

.help-modal__body ul {
  color: var(--secondary-color);
  list-style: decimal;
}

.help-modal__body li:not(:last-child) {
  margin-bottom: 1rem;
}

header {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  margin: 40px auto 20px;
  padding: 0 20px;
  display: flex;
}

header a {
  margin-left: 10px;
}

header a svg {
  height: 40px;
  fill: var(--secondary-color);
}

.separator {
  flex: 1;
}

h1 {
  letter-spacing: 20px;
  margin: 0 0 20px;
  font-size: 45px;
  font-weight: 200;
}

button {
  cursor: pointer;
}

.controls {
  flex-wrap: nowrap;
  display: flex;
}

.controls button {
  color: var(--text-color);
  border: solid 2px var(--text-color);
  background: none;
  border-radius: 30px;
  margin-right: 5px;
  padding: 10px 20px;
  font-size: 14px;
}

.no-game-padding {
  height: 80px;
}

@media only screen and (min-width: 760px) {
  .no-game-padding {
    height: 125px;
  }
}

.game {
  flex-direction: column;
  margin: 0 auto 50px;
  padding: 0 5px;
  display: flex;
}

.board {
  flex-direction: column;
  margin: auto;
  display: flex;
  position: relative;
}

.side {
  background: var(--primary-color);
  height: 120px;
  border: 7.5px solid var(--secondary-color);
  border-radius: 90px;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media only screen and (min-width: 760px) {
  .side {
    height: 180px;
    border-width: 10px;
  }
}

.pit-container {
  text-align: center;
  position: relative;
}

.pit, .hand, .captured {
  width: 45px;
  height: 45px;
  background: var(--secondary-color);
  color: var(--text-color);
  border: 0;
  border-radius: 45px;
  padding: 0;
  position: relative;
}

.pit {
  margin: 0 4px;
}

@media only screen and (min-width: 760px) {
  .board {
    width: 720px;
  }

  h1 {
    font-size: 60px;
  }

  .pit, .hand, .captured {
    width: 90px;
    height: 90px;
  }

  .pit {
    margin: 0 8px;
  }

  .game {
    padding: 0 20px;
  }
}

.hand {
  pointer-events: none;
  z-index: 2;
  background: none;
  position: absolute;
  top: 135px;
  left: 42px;
  transform: scale(1.2);
}

.hand .seed {
  -o-transition: all .3s ease-in;
  transition: all .3s ease-in;
}

.pit.disabled {
  cursor: not-allowed;
}

.pit-summary {
  min-width: 40px;
  color: var(--text-on-primary-color);
  border-radius: 5px;
  padding: 2.5px 5px;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  top: -27.5px;
  left: calc(50% - 20px);
}

.side-2 .pit-summary {
  top: unset;
  bottom: -27.5px;
}

.captured .pit-summary {
  color: var(--darker-text-on-background-color);
  top: calc(50% - 13px);
  left: calc(100% + 5px);
}

.seed {
  height: 13px;
  width: 15px;
  background: var(--seed-color);
  border: 1px solid var(--secondary-color);
  border-radius: 10px;
  position: absolute;
  top: 16px;
  left: 15px;
}

@media only screen and (min-width: 760px) {
  .seed {
    height: 20px;
    width: 25px;
    border-width: 2px;
    top: 37.5px;
    left: 30px;
  }
}

.player {
  text-align: center;
  margin: 5px 0;
  font-weight: bold;
  display: none;
}

.player .turn-badge, .player .winner-badge {
  height: 14px;
  margin: 3.5px 10px;
  font-size: 14px;
  font-style: italic;
  font-weight: 400;
  line-height: 12px;
  display: none;
  position: absolute;
}

.player .player-display {
  height: 25px;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.player .captured {
  background: 0;
  margin: auto;
}

